import { Component, Event, EventEmitter, h, Host, Method, Prop } from '@stencil/core';
import { generateUid, parseClassNames } from '@utils/utils';

@Component({
  tag: 'storm-checkbox',
  styleUrl: 'storm-checkbox.css',
  shadow: false,
})
export class StormCheckbox {
  private _uid?: string;
  private _inputEl: HTMLInputElement;

  @Prop() type: 'checkbox' | 'radio' = 'checkbox';
  @Prop() label: string;
  @Prop() labelClasses?: Record<string, boolean> | string = 'custom-control-label';
  @Prop({ mutable: true, reflect: true }) isDisabled?: boolean;
  @Prop({ mutable: true, reflect: true }) isChecked?: boolean;
  @Prop({ mutable: true, reflect: true }) isIndeterminate?: boolean;
  @Prop({ reflect: true }) isSwitch: boolean = false;
  @Prop({ reflect: true }) isInline: boolean = false;
  @Prop({ mutable: true, reflect: true }) timeStamp?: number;

  @Event() valueChanged: EventEmitter<boolean>;

  @Method()
  async check() {
    if (this.isChecked) return;
    this.isChecked = true;
    this.valueChanged.emit(this.isChecked);
  }

  @Method()
  async uncheck() {
    if (!this.isChecked) return;
    this.isChecked = false;
    this.valueChanged.emit(this.isChecked);
  }

  renderLabel() {
    if (!this.label) return;
    return (
      <label htmlFor={this._uid} class={parseClassNames(this.labelClasses)}>
        {this.label}
      </label>
    );
  }

  componentDidRender() {
    if (this._inputEl) {
      this._inputEl.indeterminate = !!this.isIndeterminate;
    }
  }

  render() {
    if (!this._uid) this._uid = generateUid('storm-checkbox');

    return (
      <Host>
        <div class={{ 'form-check': true, 'form-check-inline': this.isInline, 'form-switch': this.isSwitch }}>
          <input
            ref={el => (this._inputEl = el)}
            class={{ 'form-check-input': true }}
            id={this._uid}
            type={this.type}
            checked={this.isChecked}
            disabled={this.isDisabled}
            indeterminate={this.isIndeterminate}
            onChange={async ev => {
              ev.stopPropagation();
              if (ev.target['checked']) await this.check();
              else await this.uncheck();
            }}
          />
          {this.renderLabel()}
        </div>
      </Host>
    );
  }
}
